import React, { useEffect, useState, useRef } from "react";



import { getStore, updStore, gen16CodeId, parseDate, fetchPostH, getUrlServerSide, fetchEncryptStream, qryStore } from "../../../stores/Utils";

import { CheckBoxSlide_Cmpt, TextInput_Cmpt, CheckBox_Cmpt } from "../../../components";


import "./dashboard.css"
import { CloseModal } from "../../../components/DialogHRM";

const CheckBoxSlide = CheckBoxSlide_Cmpt();
const TextInput = TextInput_Cmpt();

const CheckBox = CheckBox_Cmpt();

const frmNm = "new_account"

const AddClient  = ({ data, confirm }) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);

	useEffect(() => {
		if (!initialize) {
			setInitialize(true);
			
			set_obs(gen16CodeId())
		}
	});

	const updFrm = (f, v, k) => {
		let frm2 = getStore(frmNm) || {};
		if(k){
			if( !frm2[k]){
				frm2[k] = {};
			}
			frm2[k][f] = v;
		}else{
			frm2[f] = v;
		}
		updStore(frmNm, frm2);
		
		set_obs(gen16CodeId())
	};


	const save  = async ()  => {
		let ffm = getStore(frmNm);

		ffm["accountId"] = gen16CodeId();

		let bdyq2 = {...getStore(qryStore())?.add_acc};
		bdyq2.params = {
			businessId:  getStore("profileUser")?.businessId
		}
		bdyq2.form=ffm;
	
		const res = fetchEncryptStream(bdyq2);
		const td = await res;
		if (td) {
			updStore(frmNm, {});
			confirm && confirm();
			data?.modalID && CloseModal({id:data?.modalID})
		}
		///let pdfUri = await generateTranslateAddClient(ffm, true, true);
		///window.open(pdfUri);
	};





	const clean = (f) => {
		updStore(frmNm, {});
		set_obs(gen16CodeId())
	};

	


	return (
		<div style={{
			maxHeight: "80vh",
			width: "560px",
			overflow: "auto",
			background: "var(--hrm_palette-general-background-b)",
			borderRadius: 13,
			padding: "10px 19px"
		}}>
			<div className="_dsplFlx ">
				<p className="form_title">
					Agregar Cuenta
				</p>
				<div className="flexSpace"/>
				<p className="group_title addBtn" onClick={clean}>
					limpiar
				</p>
			</div>

			<div className="_dsplFlx ">
				<TextInput  
					init={getStore(frmNm)?.["code"]} 
					label={"Codigo"} 
					width={120} 
					updChanges={(e)=>updFrm("code",e)}
				/>
				<div className="flexSpace"/>
				<div className="_dsplFlx " style={{padding: "18px"}}>
					<div className="flexSpace"/>
					<CheckBox data={[
							{label: "Real", id:"real"},
							{label: "Nominal", id:"nominal"}
						]}
						active={getStore(frmNm)?.["clasification"]}
						updSelect={(e)=>updFrm("clasification",e)}
					/>
					<div className="flexSpace"/>
				</div>
			</div>
			<TextInput  
				init={getStore(frmNm)?.["name"]} 
				label={"Nombre"} 
				width={520} 
				updChanges={(e)=>updFrm("name",e)}
			/>

			<div className="_dsplFlx " style={{padding: "18px"}}>
				<div className="flexSpace"/>
				<CheckBox 
					data={[
						{label: "Deudoras", id:"debit"},
						{label: "Acredoras", id:"credit"}
						
					]}
					active={getStore(frmNm)?.["type"]}
					updSelect={(e)=>updFrm("type",e)}
				/>
				<div className="flexSpace"/>
			</div>

			

			<div className="_dsplFlx " style={{padding: "18px"}}>
				<div className="flexSpace"/>
				<CheckBox 
					data={[
						{label: "Regular", id:null},
						{label: "Pago", id:"pay"},
						{label: "Cobro", id:"collect"}
					]}
					active={getStore(frmNm)?.["payOrCollect"] || null}
					updSelect={(e)=>updFrm("payOrCollect",e)}
				/>
				<div className="flexSpace"/>
			</div>

			
			<div className="_dsplFlx ">
				<div className="flexSpace"/>
				{getStore(frmNm)?.["code"] && getStore(frmNm)?.["name"] && getStore(frmNm)?.["type"] && getStore(frmNm)?.["clasification"] ?
				<div className="card">
					<button onClick={save}>
						Guardar
					</button>
				</div>
				: null}

			</div>
		

			
		</div>
	);
};

export default AddClient;

